// Decode =>  => YAML load
export async function decodeProcess(encodedData) {
    let dataObject = null;
    try {
        //  => 
        const compressedData = base64ToUint8Array(encodedData);
        // zstd
        const yamlData = await zstdDecompressString(compressedData);
        if (yamlData) {
            //console.debug(yamlData);
            // YAML => JavaScript
            dataObject = loadYAML(yamlData);
        }
    }
    catch (error) {
        console.error(":", error);
    }
    return dataObject;
}
// YAML dump =>  => Encode
export async function encodeProcess(dataObject) {
    let encodedData = null;
    try {
        // YAML => YAML
        const yamlData = dumpYAML(dataObject);
        // zstd
        const compressedData = await zstdCompressString(yamlData);
        if (compressedData) {
            //  => 
            encodedData = uint8ArrayToBase64(compressedData);
        }
    }
    catch (error) {
        console.error(":", error);
    }
    return encodedData;
}
